import React, { Component } from "react";

import axios from "axios";

import Character from "./Character.js";
import Message from "./../../Header.styles";
import Main from "./../../Main.styles";

let currentIdCounter = 1;
let maxId = 9;

class RenderChars extends Component {
  constructor() {
    super();
    this.state = {
      chars: [],
      loading: true
    };
    this.loadingText = "LOADING CHARACTERS";
    this.request = axios.create({
      baseURL: "https://rickandmortyapi.com/api/character",
      headers: {
			  "Accept": "application/json",
			  "Content-Type": "application/json"
      }
    });
  }

  renderPrevPage = () => {
    if (currentIdCounter < 9) {
      return currentIdCounter;
    } else {
      this.setState({
        loading: true
      });
      currentIdCounter = currentIdCounter - 18;
      maxId = maxId - 18;
      this.componentDidMount();
    }
  }

  renderNextPage = () => {
    this.setState({
      loading: true
    });
    if (currentIdCounter < 394) {
      this.componentDidMount();
    }
  }

  charsId() {
    let arrOfId = "";
    while (currentIdCounter <= maxId) {
      arrOfId ? arrOfId = arrOfId + "," + currentIdCounter : arrOfId = arrOfId + currentIdCounter;
      currentIdCounter++;
    }
    maxId = maxId + 9;
    console.log(arrOfId);
    return arrOfId;
  }

  componentDidMount() {
    this.request.get(`/${this.charsId()}`)
    .then(data => {
      this.setState({
        chars: data.data,
        loading: false
      })
    });
  }

  render() {
    if (this.state.loading) {
      return (
        <Message.Container>
          <Message.Title>{ this.loadingText }</Message.Title>
        </Message.Container>
      )
    } else {
      return (
        <>
          <Main.PrevPage onClick = { this.renderPrevPage }></Main.PrevPage>
          <Main.CharactersContainer>
            {
              this.state.chars.map(char => {
                return (
                  <Character name = { char.name } image = { char.image } key = { char.id }/>
                )
              })
            }
          </Main.CharactersContainer>
          <Main.NextPage onClick = { this.renderNextPage }></Main.NextPage>
        </>
      )
    }
  }
}

export default RenderChars;